.component-button {
  display: inline-flex;
  width: 33.3%;
  flex: 1 0 auto;
}

.component-button.wide {
  width: 100%;
}

.component-button.quart {
  width: 25%;
}

.component-button button {
  background-color: #eee;
  color: black;
  border: 0;
  font-size: 1.5rem;
  margin: 0 1px 0 0;
  flex: 1 0 auto;
  padding: 0;
  transition: all 0.1s ease-in-out;
}

.component-button button:active {
  opacity: 0.75;
}

.component-button:last-child button {
  margin-right: 0;
}

.component-button.orange button {
  background-color: #f5923e;
  color: white;
}

.component-button.dark button {
  background-color: #333;
  color: white;
}
